export const Person_Page = (props) => {
    console.log(props.ident);
  const  [locale,setLocale]= useState(LOCALES.RUSSIAN);
    return (
      <IntlProvider messages={messages[locale]} locale={locale}>
      <header class="header">
      <div class="head-menu">
        <div class="menu-languages">
        <ToggleButtonGroup type="radio" name="options" defaultValue={0} class="menu-languages">
      <ToggleButton 
        id="lang-ru"
        variant="outline-secondary"
        value={0}
        href="#"
        size='sm'
        onClick={()=>setLocale(LOCALES.RUSSIAN)}
        >
        <FormattedMessage id="Rulang"></FormattedMessage>
      </ToggleButton>
      <ToggleButton id="lang-en" variant="outline-secondary" value={1} href="#" size='sm' onClick={()=>setLocale(LOCALES.ENGLISH)} >

        <FormattedMessage id="Englang"></FormattedMessage>
      </ToggleButton>
    </ToggleButtonGroup>
        </div>
        <div class="menu-navigation-buttons">
          <Button href="./List" className="list-figures" variant="outline-success"><p><FormattedMessage id="ListBtn"></FormattedMessage></p></Button>        
          <Button href="./" className="main-page" variant="outline-primary"><p><FormattedMessage id="HomeBtn"></FormattedMessage></p></Button>        
        </div>
      </div>
    </header>
      <h1 className="text-coloring"><FormattedMessage id="AboutDoer">  :</FormattedMessage></h1>
      <section className="field">
        <div className="main_div">
          <Person_info ident={props.ident}></Person_info>
        </div>
      </section>
      <TimeLine ident={props.ident}></TimeLine>
      <h2 className="text-coloring"><FormattedMessage id="AuthorsWorks"> :</FormattedMessage></h2>
      <Gal ident={props.ident}></Gal>
      <h2 className="text-coloring"><FormattedMessage id="Video">:</FormattedMessage></h2>
      <section className="field">
        <Vid ident={props.ident}></Vid>
      </section>
      <h2 className="text-coloring"><FormattedMessage id="AuthorsPlace">  :</FormattedMessage></h2>
      <section className="field">
        <Maps ident={props.ident}></Maps>
      </section>
     
      </IntlProvider>
    );
 
}

export default Person_Page;